import React from 'react';
import ReactDOM from 'react-dom';
 import label from './label.png';

//class component
class PriceTag extends React.Component {
  constructor(props) {
    super(props);
    this.lab = props.lab;
    this.factory = props.control;
    this.state = {
      height: this.lab.room.height,
      area: this.lab.room.area,
      bACH: this.lab.room.baseACH,
      tACH: this.lab.targetACH,
    };
  }
  render() {
    return (
      <div className='Room'>
        <div className='dropdowns'>
        </div>
        <div id='titleHeader'>
          <h2>Price</h2>
        </div>
        <div id='tagContent'>
          <div id='tag'>
            <div id='tagTxt'>
              <p id='priceStatement'>
                Change the room settings to get a price estimate.
              </p>
            </div>
          <img id='limg' src={label} alt="price tag"/>
          </div>
        </div>
        <div id='price'>
          <h4>$$$</h4>
        </div>
      </div>

    );
  }
}
export default PriceTag
