import React from 'react';
import ReactDOM from 'react-dom';

//class component
class Filter extends React.Component {
  constructor(props) {
    super(props);
    this.lab = props.lab;
    this.factory = props.control;
    this.state = {
      height: this.lab.room.height,
      area: this.lab.room.area,
      bACH: this.lab.room.baseACH,
      tACH: this.lab.targetACH,
    };
    this.selectFilter = 0;
  } selectUp = event => {
    this.selectFilter = this.selectFilter + 1;
    let maxFilters = Object.keys(this.lab.catalogue).length;
    this.lab.selectFilter(this.selectFilter);
    this.lab.getRoomValues();
    let vals = this.lab.getFilterValues();
    console.log(vals);
    console.log(maxFilters);
  }
  selectDown = event => {
   this.selectFilter = this.selectFilter - 1;
   this.lab.selectFilter(this.selectFilter);
   this.lab.getRoomValues();
   let vals = this.lab.getFilterValues();
   console.log(vals);
 }
  render() {
    this.lab.selectFilter(0);
    this.lab.getRoomValues();
    let vals = this.lab.getFilterValues();
    let m = vals['Model'];
    let l = vals['Source'];
    console.log(vals);
    return (
      <div className='Room'>
        <a href={l}>
          <div id='filterName'>
            <h2>{m}<span id='infoButt' className="material-icons">info</span></h2>
          </div>
        </a>
        <div className='dropdowns'>
        </div>
        <div id='filterUnits'>
          <div className='fUnit'>
            <img id='roomImg' src='https://github.com/TheMemeticist/How-Many-Corsi-Rosenthal-Boxes/blob/main/assets/CR-Cube.gif?raw=false'/>
          </div>
          <h2 id='x2'>x</h2>
          <div className='fUnit'>
            <p id='nFilters'>n</p>
          </div>
        </div>
        <div id='filterEstimate'>
          <p id='fEst'>You need an estimated [n-Filters] for your room.</p>
        </div>
        <div id='orderButtons'>
          <a href='https://thememeticist.github.io/How-Many-Corsi-Rosenthal-Boxes/order.html'>
            <div id='buildButton'>
              <h3>Build <span class='material-icons md-light'>build</span></h3>
            </div>
          </a>
          <a href='https://thememeticist.github.io/How-Many-Corsi-Rosenthal-Boxes/order.html'>
            <div id='buyButton'>
              <h3>Buy <span class='material-icons md-light'>shopping_cart</span></h3>
            </div>
          </a>
        </div>
      </div>

    );
  }
}
export default Filter
